.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.text {
   font-size: 2rem;
}

.Sidebar {

  
margin-top : 20px;
margin-left: 30px;
margin-bottom: 20px;
border-radius: 10px;
width: 340px;
background: #777776;
overflow: scroll;


}



@media screen and (max-height:1000px) {
  .Sidebar {
    height:190vh;
    
  }
}




